
div.comment-root{
  opacity: 0;
  min-height: 100%;
  transition: all 0.5s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
div.comment-root.show{
  opacity: 1;
}
