  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding:0 0.8rem ;
  overflow: hidden;
  box-sizing: border-box;
}
div.button-viewport{
  position: relative;
  height:3.2rem;
