}
div.tags-pre>button{
  height:1.4rem;
  min-width:2rem;
  display: inline-block;
  padding:0;
  margin:0 0.25rem 0.2rem 0.25rem;
  border-radius: 0.3rem;
  background: #FFFFFF;
  border:1px solid #6C77C9;
  font-size: 0.7rem;
}
div.tags-pre{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
div.alert.show{
  transform: translateY(0);
  background-color: red;
  opacity: 1;
}
div.alert>p{
  text-align: center;
  line-height: 3rem;
  font-size: 0.8rem;
  margin:0;
  color: #FFF;
}
div.alert{
  height: 3rem;
  width:100%;
  position: fixed;
  top:0;
  transform: translateY(-100%);
  transition: all 0.5s;
  z-index: 100;
  opacity: 0;
}
.mint-swipe,.mint-swipe-items-wrap{
  overflow:hidden;position:relative;height:100%
}
.mint-swipe-items-wrap>div{
  position:absolute;transform:translateX(-100%);width:100%;height:100%;display:none
}
.mint-swipe-items-wrap>div.is-active{
  display:block;transform:none
}
.mint-swipe-indicators{
  position:absolute;bottom:0.3rem;left:50%;transform:translateX(-50%)
}
.mint-swipe-indicator{
  width:0.3rem;height:0.3rem;display:inline-block;border-radius:100%;background:#AAA;opacity:.8;margin:0 3px
}
.mint-swipe-indicator.is-active{
  background:#2c3e50;
}
.input-area-upload {
  padding: 0;
  margin: 0.5rem 0.5rem 0;
  list-style: none;
  /* text-align: center; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.input-area-upload > li {
  width: 4.3rem;
  height: 4.3rem;
  display: inline-block;
  position: relative;
}
.input-area-upload .choose-image {
  width: 100%;
  height: 100%;
}
.input-area-upload input {
  display: none;
}
.input-area-upload canvas {
  display: none;
}
.images-to-upload img {
  width: 100%;
  height: 100%;
}
.image-upload-delete {
  display: block;
  position: absolute;
  left: -0.4rem;
  top: -0.4rem;
  width: 1rem;
  height: 1rem;
  font-size: 1rem;
  line-height: 1rem;
  text-align: center;
  background: #FF0000;
  border-radius: 50%;
  color: #FFFFFF;
  transform: rotate(45deg);
}



